import React, { useState, useEffect, useRef } from 'react';
import './HeaderStyles.css';

const hardcodedData = [
  { StationName: 'Kyiv', Latitude: '50.4501', Longitude: '30.5234' },
  { StationName: 'Odesa', Latitude: '46.4825', Longitude: '30.7233' },
  { StationName: 'Kharkiv', Latitude: '49.9935', Longitude: '36.2304' },
  { StationName: 'Dnipro', Latitude: '48.4647', Longitude: '35.0462' },
  { StationName: 'Lviv', Latitude: '49.8397', Longitude: '24.0297' },
  { StationName: 'Zaporizhzhia', Latitude: '47.8388', Longitude: '35.1396' },
  { StationName: 'Poltava', Latitude: '49.5883', Longitude: '34.5514' },
  { StationName: 'Chernihiv', Latitude: '51.5055', Longitude: '31.2849' },
  { StationName: 'Kryvyi Rih', Latitude: '47.9105', Longitude: '33.3918' },
  { StationName: 'Mariupol', Latitude: '47.0971', Longitude: '37.5413' }
];

const Header = ({ toggleSidebar, onSubmit, toggleTheme, darkTheme, selectedStationCoords, mapRef }) => {
  const [selectedStation, setSelectedStation] = useState('');
  const [searchText, setSearchText] = useState('');
  const [isListOpen, setIsListOpen] = useState(false);
  const dropdownRef = useRef(null);

  useEffect(() => {
    if (selectedStationCoords && mapRef.current) {
      const { latitude, longitude } = selectedStationCoords;
      mapRef.current.setView([latitude, longitude], 12);
    }
  }, [selectedStationCoords]);

  const handleStationChange = (station) => {
    setSelectedStation(station.StationName);
    setIsListOpen(false);
    if (onSubmit) {
      onSubmit({ latitude: station.Latitude, longitude: station.Longitude });
    }
  };

  const handleInputChange = (event) => {
    setSearchText(event.target.value);
    setIsListOpen(true);
  };

  const handleOutsideClick = (event) => {
    if (dropdownRef.current && !dropdownRef.current.contains(event.target)) {
      setIsListOpen(false);
    }
  };

  useEffect(() => {
    document.addEventListener('mousedown', handleOutsideClick);

    return () => {
      document.removeEventListener('mousedown', handleOutsideClick);
    };
  }, []);

  const filteredStations = hardcodedData.filter(station =>
    station.StationName.toLowerCase().includes(searchText.toLowerCase())
  );

  return (
    <div className="header">
      <h2>cargo location</h2>
      <input type="text" value={searchText} onChange={handleInputChange}
        placeholder="Start typing station name"
        className="input"
      />
      {isListOpen && (
        <div ref={dropdownRef} className="filtered-stations">
          {filteredStations.map((station, index) => (
            <div key={index} onClick={() => handleStationChange(station)}>
              {station.StationName}
            </div>
          ))}
        </div>
      )}

      <button className='buttonNightMode' onClick={toggleTheme}>
        {darkTheme ? 'Light Mode' : 'Night Mode'}
      </button>
      <button onClick={toggleSidebar} className="button"></button>
    </div>
  );
};

export default Header;