import * as XLSX from 'xlsx';
import './mapUtils.css';

// Function to generate table content for the popup
export const generateTableContent = (markers) => {
    let tableContent = '<div style="width: 100%; margin: auto; overflow-x: auto; overflow-y: auto; color: black; scrollbar-width: thin;">';
    tableContent += `<table style="border-collapse: collapse; margin: auto; width: 99%;">
    <thead>
        <tr>
            <th>    Wagon          </th> 
            <th>    Train Index     </th>
            <th>    Station Code    </th>
            <th>    Actual Station  </th>
            <th>    Operation Date  </th>
            <th>    From Code       </th>
            <th>    Departure Station</th>
            <th>    Start Date      </th>
            <th>    Sender          </th>
            <th>    Receiver        </th>
            <th>    Cargo Code      </th>
            <th>    Cargo           </th>
            <th>    Destination     </th> 
            <th>    Client          </th>           
            <th>    Empty           </th>         
            <th>    Simple          </th>
            </tr>
    </thead>
    <tbody>`;

    markers.forEach((marker) => {
        const popupContent          = marker.getPopup().getContent();
        const [, railwayNumber]     = popupContent.match(/<span class="valueContent">(.*?)<\/span>/); 
        const [, indexTraine]       = popupContent.match(/Train Index: <span class="valueContent">(.*?)<\/span>/);
        const [, actualStationCode] = popupContent.match(/Station Code: <span class="valueContent">(.*?)<\/span>/);
        const [, stationActual]     = popupContent.match(/Actual Station: <span class="valueContent">(.*?)<\/span>/);
        const [, dateValue]         = popupContent.match(/Operation Date: <span class="valueContent">(.*?)<\/span>/);
        const [, stationFromIndex]  = popupContent.match(/Departure Station Code: <span class="valueContent">(.*?)<\/span>/);
        const [, stationFrom]       = popupContent.match(/Departure Station: <span class="valueContent">(.*?)<\/span>/);
        const [, dateStart]         = popupContent.match(/Start Date: <span class="valueContent">(.*?)<\/span>/);
        const [, cargoFrom]         = popupContent.match(/Sender: <span class="valueContent">(.*?)<\/span>/);
        const [, cargoTo]           = popupContent.match(/Receiver: <span class="valueContent">(.*?)<\/span>/);
        const [, cargoCode]         = popupContent.match(/Cargo Code: <span class="valueContent">(.*?)<\/span>/);
        const [, cargo]             = popupContent.match(/Cargo: <span class="valueContent">(.*?)<\/span>/);
        const [, stationTo]         = popupContent.match(/Destination: <span class="valueContent">(.*?)<\/span>/);
        const [, client]            = popupContent.match(/Client: <span class="valueContent">(.*?)<\/span>/);    
        const [, isEmpty]           = popupContent.match(/Empty Wagon: <span class="valueContent">(.*?)<\/span>/);
        const [, simple]            = popupContent.match(/Simple: <span class="valueContent">(.*?)<\/span>/);

        const dateObj                = new Date(dateValue);
        const formattedDateValue     = dateObj.toLocaleDateString('en-GB');
        const formattedTimeValue     = dateObj.toLocaleTimeString('en-GB');
        const formattedDateTimeValue = `${formattedDateValue} ${formattedTimeValue}`;
        
        const dateStartObj           = new Date(dateStart);
        const formattedDateStart     = dateStartObj.toLocaleDateString('en-GB');
        const formattedTimeStart     = dateStartObj.toLocaleTimeString('en-GB');
        const formattedDateTimeStart = `${formattedDateStart} ${formattedTimeStart}`;
        
        tableContent += `
        <tr>
            <td>                             ${railwayNumber}</td>
            <td>                             ${indexTraine}</td>
            <td>                             ${actualStationCode}</td>
            <td>                             ${stationActual}</td>
            <td>                             ${formattedDateTimeValue}</td>
            <td style="text-align: center;"> ${stationFromIndex}</td>
            <td>                             ${stationFrom}</td>
            <td style=" text-align: start;"> ${formattedDateTimeStart}</td>
            <td style=" text-align: start;"> ${cargoFrom}</td>
            <td style=" text-align: center;">${cargoTo}</td>
            <td style=" text-align: center;">${cargoCode}</td>
            <td style=" width: 175px;">      ${cargo}</td>
            <td>                             ${stationTo}</td>
            <td style=" width: 135px">       ${client ? client : 'no data...'}</td>
            <td style=" text-align: center;">${isEmpty}</td>
            <td style=" text-align: center;">${simple}</td>
            </tr>`;
    });
    tableContent += `</tbody></table></div>`;
  
    return tableContent;
};

// Function to create a modal window with the table
export const createModal = (tableContent, mapRef) => {
    // Lock map zoom
    if (mapRef && mapRef.current) {
      mapRef.current.setMaxZoom(mapRef.current.getZoom());
    }

    let modal = document.createElement('div');
    modal.style.className       = 'modal-container';
    modal.style.position        = 'fixed';
    modal.style.top             = '0';
    modal.style.left            = '0';
    modal.style.width           = '100%';
    modal.style.height          = '100%';
    modal.style.backgroundColor = 'rgba(0, 0, 0, 0.7)';
    modal.style.display         = 'flex';
    modal.style.justifyContent  = 'center';
    modal.style.alignItems      = 'center';
    modal.style.zIndex          = '1000';

    let content = document.createElement('div');
    content.className           = 'table-container';
    content.style.padding       = '5px';
    content.style.maxHeight     = '80vh';
    content.style.width         = '95%';
    content.style.overflowY     = 'auto';
    content.style.fontSize      = '11px';

    let closeButton = document.createElement('button');
    closeButton.innerHTML       = 'X';
    closeButton.className       = 'close-button-table';
    closeButton.addEventListener('click', () => {
        modal.remove();
        // Unlock map zoom
        if (mapRef && mapRef.current) {
          mapRef.current.setMaxZoom(); 
        }
    });

    let exportButton = document.createElement('button');
    exportButton.innerHTML = '<i class="fas fa-file-excel"></i>';
    exportButton.className = 'export-button-table'; 
    exportButton.addEventListener('click', () => {
        exportToExcel(content.querySelector('table'));
    });

    content.innerHTML = tableContent;

    modal.appendChild(closeButton);
    modal.appendChild(exportButton);
    modal.appendChild(content);

    document.body.appendChild(modal);
};

const exportToExcel = (table) => {
    const ws = XLSX.utils.table_to_sheet(table);
    const wb = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, ws, 'Sheet1');
    XLSX.writeFile(wb, 'table.xlsx');
};

// Function to generate popup content for a marker
export const generatePopupContent = (item) => {
    return `
    <div>
      <p><span class="valueContent">${item.RailwayNumber}</span></p> 
      <p>Train Index: <span class="valueContent">${item.IndexTraine}</span></p>
      <p>Station Code: <span class="valueContent">${item.ActualStationCode}</span></p> 
      <p>Actual Station: <span class="valueContent">${item.StationActual}</span></p>
      <p>Operation Date: <span class="valueContent">${item.DateOperation}</span></p> 
      <p>Departure Station Code: <span class="valueContent">${item.StationFromIndex}</span></p> 
      <p>Departure Station: <span class="valueContent">${item.StationFrom}</span></p>
      <p>Start Date: <span class="valueContent">${item.DateStart}</span></p> 
      <p>Sender: <span class="valueContent">${item.CargoFrom}</span></p>
      <p>Receiver: <span class="valueContent">${item.CargoTo}</span></p> 
      <p>Cargo Code: <span class="valueContent">${item.CargoCode}</span></p>
      <p>Cargo: <span class="valueContent">${item.Cargo}</span></p>
      <p>Destination: <span class="valueContent">${item.StationTo}</span></p>
      <p>Client: <span class="valueContent">${item.Client ? item.Client : 'no data...'}</span></p>   
      <p>Empty Wagon: <span class="valueContent">${item.Empty ? 'Yes' : 'No'}</span></p>
      <p>Simple: <span class="valueContent">${item.Simple}</span></p>
    </div>
  `;
};
